"use client";

import React, { useState } from "react";
import { useSpring, animated } from "@react-spring";


const TodoList = () => {
  const [todos, setTodos] = useState([]);
  const [newTodo, setNewTodo] = useState("");
  const [editingId, setEditingId] = useState(null);
  const [editingText, setEditingText] = useState("");

  const animation = useSpring({
    from: { opacity: 0, transform: "scale(0.95)" },
    to: { opacity: 1, transform: "scale(1)" },
    config: { tension: 120, friction: 14 },
  });

  const handleAddTodo = () => {
    if (!newTodo.trim()) return;
    setTodos([{ id: Date.now(), text: newTodo, completed: false }, ...todos]);
    setNewTodo("");
  };

  const handleToggleCompleted = (id) => {
    setTodos(todos.map((todo) => (todo.id === id ? { ...todo, completed: !todo.completed } : todo)));
  };

  const handleDeleteTodo = (id) => {
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  const handleEditTodo = (id, text) => {
    setEditingId(id);
    setEditingText(text);
  };

  const handleSaveEdit = (id) => {
    setTodos(todos.map((todo) => (todo.id === id ? { ...todo, text: editingText } : todo)));
    setEditingId(null);
    setEditingText("");
  };

  const handleClearAll = () => {
    setTodos([]);
  };

  return (
    <div
      className="min-h-screen bg-gradient-to-br from-pink-200 via-blue-200 to-green-200 flex items-center justify-center p-6"
    >
      <animated.div
        style={animation}
        className="w-full max-w-4xl p-8 bg-white/70 rounded-lg shadow-lg backdrop-blur-md border border-gray-200"
      >
        <h1 className="text-4xl font-bold text-gray-800 mb-6 text-center">To-Do List</h1>
        <form onSubmit={(e) => e.preventDefault()} className="flex mb-6">
          <input
            type="text"
            value={newTodo}
            onChange={(e) => setNewTodo(e.target.value)}
            placeholder="Add a task..."
            className="flex-1 p-4 text-lg text-gray-700 bg-white border border-gray-300 rounded-l-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
          />
          <button
            type="submit"
            onClick={handleAddTodo}
            className="p-4 bg-blue-500 text-white font-semibold rounded-r-lg hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-400"
          >
            Add
          </button>
        </form>
        <ul className="space-y-4">
          {todos.map((todo) => (
            <li
              key={todo.id}
              className={`flex items-center p-4 bg-white shadow-md rounded-lg border-l-4 ${
                todo.completed ? "border-green-500" : "border-blue-500"
              }`}
            >
              <input
                type="checkbox"
                checked={todo.completed}
                onChange={() => handleToggleCompleted(todo.id)}
                className="mr-4 h-6 w-6 text-blue-500 focus:ring-blue-400"
              />
              {editingId === todo.id ? (
                <input
                  type="text"
                  value={editingText}
                  onChange={(e) => setEditingText(e.target.value)}
                  className="flex-1 p-2 text-lg border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
                />
              ) : (
                <span
                  className={`flex-1 text-lg ${
                    todo.completed ? "line-through text-gray-400" : "text-gray-800"
                  }`}
                >
                  {todo.text}
                </span>
              )}
              {editingId === todo.id ? (
                <button
                  onClick={() => handleSaveEdit(todo.id)}
                  className="ml-2 p-2 text-green-600 bg-green-100 rounded-lg hover:bg-green-200"
                >
                  Save
                </button>
              ) : (
                <button
                  onClick={() => handleEditTodo(todo.id, todo.text)}
                  className="ml-2 p-2 text-blue-600 bg-blue-100 rounded-lg hover:bg-blue-200"
                >
                  Edit
                </button>
              )}
              <button
                onClick={() => handleDeleteTodo(todo.id)}
                className="ml-2 p-2 text-red-600 bg-red-100 rounded-lg hover:bg-red-200"
              >
                Delete
              </button>
            </li>
          ))}
        </ul>
        {todos.length > 0 && (
          <div className="mt-6 flex justify-center">
            <button
              onClick={handleClearAll}
              className="p-4 bg-red-500 text-white font-semibold rounded-lg hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-400"
            >
              Clear All
            </button>
          </div>
        )}
      </animated.div>
    </div>
  );
};

export default TodoList;
